@import url('https://fonts.googleapis.com/css?family=Montserrat:700|Pacifico');

.App {
  width: 100%;
  height: 100%;
  background: url("./images/background.jpg");
  background-position: 60% 100%;
}

@media screen and (min-width: 1600px) {
  .App {
    background-position: initial;
    background-size: cover;
  }
}

body, html, #root {
  width: 100%;
  height: 100%;
}

.modal_container {
  width: 360px;
  height: 290px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  background: url('./images/notification_panel.png');
  background-size: cover;
}

.modal_body {
  padding: 40px;
  margin-top: 45px;
  text-align: center;
}

.modal_text {
  font-family: Pacifico, Helvetica, serif;
  color: #28595d;
  text-align: center;
  font-size: 18px;
}

.modal_button {
  margin-top: 10px;
  display: inline-block;
  padding: .50rem 1rem;
  border-radius: 10rem;
  color: #fff;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: .15rem;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.modal_button:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #28595d;
  border-radius: 10rem;
  z-index: -2;
}

.modal_button:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: #214b4f;
  transition: all .3s;
  border-radius: 10rem;
  z-index: -1;
}

.modal_button:hover {
  color: #ffffff;
}

.modal_button:hover:before {
  width: 100%;
}

.menu_container {
  display: flex;
  justify-content: center;
}

.menu_wrapper {
  position: relative;
  width: 100%;
  max-width: 375px;
}

.menu {
  height: 70px;
  background: url('./images/horisontal_panel.png');
  background-size: 460px 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px 10px;
  margin: 0 auto;
  z-index: 2;
  position: relative;
}

.menu_item {
  position: relative;
}

.menu_thumb {
  width: 40px;
  height: 40px;
  padding: 0 10px 10px;
}

.menu_sub {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50px;
  left: 0;
  z-index: 1;
  background: url('./images/vertical_panel.png');
  background-size: 70px 286px;
  padding: 10px 0 20px;
}

.menu_sub .menu_thumb {
  padding: 10px;
  width: 50px;
}

.sub1 {
  left: 30px;
}

.sub2 {
  left: 155px;
}

.sub3 {
  left: 280px;
}